@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content{margin-left:auto;margin-right:auto;background-color:#FAFBFC;padding:2rem;border:1px;border-radius:5px}

body{font-family:'Roboto', sans-serif}.app-container{margin-top:2rem}

.header{background-color:#24292E}.link{color:#fff;font-weight:400}.gh-logo{width:8rem;padding-right:1rem;margin-left:0.5rem}

